.DataAnalystProjects {
    width: 100vw;
    height: 100vh;
    overflow: scroll;
}

.DataAnalystProjects > h1 {
    color: white;
    font-size: 2.25rem;
    font-weight: 500;
    text-align: center;
    margin: 4rem 0;
}

.tableau {
    display: flex;
    justify-content: center;
    align-items: center;
}

.image--tableau {
    width: 30rem;
    margin-right: 2rem;
    border-radius: 0.5rem;
}

.text-container {
    max-width: 30rem;
    margin-left: 2rem;
    text-align: block;
}

.tableau-title {
    white-space: pre-line;
    font-size: 1.5rem;
    color: white;
}

.tableau-description {
    white-space: pre-line;
    font-size: 1.1rem;
    line-height: 1.5rem;
    color: white;
}
