.SoftwareDevelopmentProjects {
    width: 100vw;
    height: 100vh;
    overflow: scroll;
}

.SoftwareDevelopmentProjects > h1 {
    color: white;
    font-size: 2.25rem;
    font-weight: 500;
    text-align: center;
    margin: 4rem 0;
}

.quizzical {
    display: flex;
    justify-content: center;
    align-items: center;
}

.image--quizzical {
    width: 15rem;
    margin-right: 2rem;
    border-radius: 0.5rem;
}

.text-container {
    margin-left: 2rem;
    text-align: block;
}

.quizzical-title {
    white-space: pre-line;
    font-size: 1.5rem;
    color: white;
}

.quizzical-description {
    white-space: pre-line;
    font-size: 1.1rem;
    line-height: 1.5rem;
    color: white;
}

.pomodoro-timer {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 4rem;
}

.image--pomodoro-timer {
    width: 25rem;
    margin-right: 2rem;
    border-radius: 0.5rem;
}

.pomodoro-title {
    white-space: pre-line;
    font-size: 1.5rem;
    color: white;
}

.pomodoro-description {
    white-space: pre-line;
    font-size: 1.1rem;
    line-height: 1.5rem;
    color: white;
}

.react-calculator {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 4rem;
}

.image--react-calculator {
    width: 15rem;
    margin-right: 2rem;
    border-radius: 0.5rem;
}

.calculator-title {
    white-space: pre-line;
    font-size: 1.5rem;
    color: white;
}

.calculator-description {
    white-space: pre-line;
    font-size: 1.1rem;
    line-height: 1.5rem;
    color: white;
}

.drum-machine {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 4rem;
    margin-bottom: 8rem;
}

.image--drum-machine {
    width: 25rem;
    margin-right: 2rem;
    border-radius: 0.5rem;
}

.drum-machine-title {
    white-space: pre-line;
    font-size: 1.5rem;
    color: white;
}

.drum-machine-description {
    white-space: pre-line;
    font-size: 1.1rem;
    line-height: 1.5rem;
    color: white;
}

@media screen and (max-width: 720px) {
    body {
        overflow-y: scroll;
    }

    .SoftwareDevelopmentProjects {
        margin-top: 16rem;
    }

    .grid-container {
        display: grid;
        grid-template: auto auto / repeat(1, 1fr);
        gap: 1.5rem;
        padding: 1.5rem 0;
    }
}

@media screen and (max-width: 420px) {
    .image--quizzical {
        max-width: 7rem;
        border-radius: 0.5rem;
    }

    .image--pomodoro-timer {
        max-width: 15rem;
        border-radius: 0.5rem;
    }

    .image--react-calculator {
        max-width: 5rem;
        border-radius: 0.5rem;
    }

    .image--drum-machine {
        max-width: 15rem;
        border-radius: 0.5rem;
    }

    .text-container {
        max-width: 10rem;
    }
}
